      mixModalVisible: false,
      dSelectPayAccountVisible: false,
      selectProductAttrVisible: false,
      selectAddressVisible: false,
      dUploadModalVisible: false,
      selectPeopleVisible: false,
      mixSearchModel: { name: 1 },
      accountModel: null,
    };
  },
  created() {
    console.log(GlMapPrototype());
  },
  methods: {
    onDrag() {},
    customCell(num, cell, index) {
      // 处理合并列的情况
      console.log(cell, index);
      return {
        attrs: {
          colSpan: num,
        },
      };
    },
    searchResultDeal(res) {
      console.log(res);
    },
    handleConfirm(result) {
